import React, { useEffect, useState } from "react";
import { 
  CssBaseline, 
  Grid,
  Typography,
  Container,
	Collapse,
} from "@mui/material";

import { getOfferId, getLang } from "../../helper/utils";
import { prices, comercials, offers } from "../../config/product";
import useInput from "../../hooks/use-Input";
import useHttp from "../../hooks/use-Http";
import PricingTableColumn from "./PricingTableColumn";

// import Link from '@mui/material/Link';
import { createTheme, ThemeProvider } from "@mui/material/styles";
import './LandingPage.scss';
import { useSearchParams } from "react-router-dom";
import { Box } from "@mui/material";

import FAQs from "./FAQ";
import Footer from "../../layout/Footer";
import Header from "../../layout/Header";
import Tracking from "../../components/Tracking/Tracking";
import UnikbaseAppIllustration from "./UnikbaseAppIllustration";
import Video from "./Video";
import Page from "../../layout/Page";

const REDIRECT_URL = process.env.REACT_APP_REDIRECT_URL || "https://unikbase.com";
const TPK_SIGNUP_URL = process.env.REACT_APP_TPK_SIGNUP_URL;
const STRIPE_PAYMENT_URL = process.env.REACT_APP_STRIPE_PAYMENT_URL || "https://dev1.unikbase.dev/meveo/rest/stripe-checkout";

export function useUrlParam(key, def = '') {
	const [searchParams] = useSearchParams();
	const urlParams = new URLSearchParams(window.location.search);
	let value = urlParams.get(key);
	if ( !value ) value = searchParams.get(key);

	return value || def;
}

const LandingPageContent = ( props ) => {
	const [offer, setOffer] = useState({});
	const [status, setStatus] = useState('idle');
	
	let tpk_jwt = useUrlParam("token");
	let lang = useUrlParam("lang");
	if (!lang) lang = navigator.language || navigator.userLanguage;
	//Converting tpk_id to get price from table
	let tpk_id = useUrlParam('tpk_id');
	let image = useUrlParam('image');
	let value = useUrlParam('value', 0);
	let token_name = useUrlParam('name');
	let token_description = useUrlParam('description');
	let email = useUrlParam('email', 'Email@mail.com');

	if (!token_name || token_name.length === 0) {
		token_name = !token_description ? 'N/A' : token_description.substring(0, 25);
	}

	const language = getLang(); //Replace it later by func
	const pageTexts = language.pages.landing_page;

	let pricingTables = pageTexts.pricing_table || {};

	const {
		isLoading: isLoadingPost,
		hasError: httpPostError,
		sendRequest: sendPostRequest,
		setIsLoading: setIsLoadingPost,
		setHttpError: setHttpErrorPost,
	} = useHttp({ 
		url: STRIPE_PAYMENT_URL, 
		method: "POST"
	});
	// const {isLoading:isLoadingGet,hasError:httpGetError,sendRequest:sendGetRequest,setIsLoading:setIsLoadingGet,setHttpError:setHttpErrorGet} = useHttp({url:'https://checkout.stripe.com',method: 'GET'})

	useEffect(() => {
		if ( !tpk_id ) {
			window.location.replace(REDIRECT_URL);
			return;
		}
		
		// let offerId = getOfferId(tpk_id);
		// fixed offer
		let currentOffer = offers.find((item) => item.id === 0) 
		setOffer(currentOffer.columns);
	}, [tpk_id]);

	////

	const submitHandler = (data) => {
		let payload = {
			email: email,
			tpk_id: tpk_id.startsWith("TPK_") ? tpk_id : "TPK_" + tpk_id, // Adding the "TPK_" prefix to the tpk_id to get the correct stripe config in backend
			price_id: data.price_id,
			value: ""+data.value, //price_item
			image: image,
			description: token_description,
			name: token_name,
			commercialOfferCode: data?.code
		};
		setStatus('loading');
		if ( data.value <= 0 ) {
			// Send to collector page
			const signupLink = TPK_SIGNUP_URL + `?tpk_id=${tpk_id}&email=${email}&name=${encodeURIComponent(token_name)}&value=${value}&description=${encodeURIComponent(token_description)}&image=${encodeURIComponent(image)}&plan=${data.value || 'free'}&commercialOfferCode=${data?.code}&tpk_token=${encodeURIComponent(tpk_jwt)}`;

			window.location.replace(signupLink)
		} else {
			// send to stripe
			sendPostRequest(payload)
				.catch((error) => {
					setIsLoadingPost(false);
					setHttpErrorPost(error.message);
				})
				.then((url) => {
					if (url) {
						let session_id = url
							.split("https://checkout.stripe.com/c/pay/")
							.join("");
						session_id = session_id.split('#')[0]
						
						localStorage.setItem("session_id", session_id);
						localStorage.setItem(`_tpk_request_${session_id}`, JSON.stringify({
							...payload,
							token_name,
							token_description,
							image,
							currency: data?.currency || '€'
						}));
						window.location.replace(url)
					}
				});
		}
	};
	
	return (
		<Page status={status}>
			{!!tpk_id &&
					<Container 
						maxWidth="lg" 
						component="section"
						className="section"
					>
						<Tracking order={tpk_id} />

						<div className="row">
							<div className="unikbase-data col col--1" style={{ paddingLeft: 0, paddingRight: 0 }}>
								
								<div className="row">
									<div id="product" className="col col--1-3 bg--white">
										<div className="product">
											<div className="product__thumbnail thumbnail">
												<div className="thumbnail__wrap" style={{
													backgroundImage: `url(${image})`,
												}}>
												</div>
											</div>
											<h3 className="product__title">
												{token_name || 'Object name'}
											</h3>
											<Typography className="product_description" component="div" variant="body1">
												{token_description || ''}
											</Typography>
										</div>
									</div>

									<div 
										id="about-tpk"
										className="col col--2-3" 
										style={{ 
											position: "relative" 
										}}
									>
										<div className="row content">
											<div className="col col--2-3">
												<Typography component="h3" variant="body1">
													{pageTexts.title}
												</Typography>

												<Typography component="h4" variant="body1">
													{pageTexts.sub_title}
												</Typography>
											</div>
										</div>
										<UnikbaseAppIllustration />
									</div>
								</div>

								<div id="pricing-plans" className="row">
									<div className="col col--1">
										<div className="row">
											{!!pricingTables.columns && pricingTables.columns.map((column, idx) => {
													let price = comercials.find(el => el.id === idx)
													return (
															<PricingTableColumn 
																key={`offer-${idx}`}
																id={`column-${idx}`}
																price={price}
																image={image} 
																column={column} 
																callback={submitHandler} 
																imageType={idx >= 1 ? '3D' : ''} 
																options={pricingTables?.options}
															/>
													)
											})}
										</div>

										<div className="pricing-plan-note row">
											<div className="col col--1" dangerouslySetInnerHTML={{ __html: pricingTables.option_note }} />
										</div>
									</div>
								</div>
							</div>
						</div>
					</Container>}

				<Container
					maxWidth="lg"
					component="section"
					className="section"
					id="about-digital-passport"
				>
					<div className="row">
						<div className="col col--1-3 bg--gray">
							<Video />
						</div>
						<div className="col bg--blue col--2-3">
							<Typography component="h3" variant="body1">{pageTexts.what_is_digital_passport}</Typography>

							<Typography component="div" variant="body1" dangerouslySetInnerHTML={{__html: pageTexts.what_is_digital_passport_paragraph}}>
							</Typography>
						</div>
					</div>
				</Container>

				<Container
					maxWidth="lg"
					component="section"
					className="section"
					id="benefits"
				>
					<div className="row">
						<div className="col col--2-3 bg--gray">
							<Typography component="h3" varaint="body1">{pageTexts.benefits}</Typography>
							<ul className="list">
								{!!pageTexts.benefit_items && pageTexts.benefit_items.map((i, idx) => (
									<li key={`benefit-item-key-${idx}`} className="item">
										<span className="item__title">{i.title}</span>
										{i.content}
									</li>
								))}
							</ul>
						</div>
						<div className="col col--1-3 benefit-cover-image" style={{
							backgroundImage: `url(${require('../../assets/benefit-bg.png')})`,
							backgroundSize: 'cover',
							backgroundRepeat: 'no-repeat',
							backgroundPosition: 'center'
						}}>
						</div>
					</div>
				</Container>
		</Page>
	);
}

export default LandingPageContent;
