import React, { useCallback, useEffect } from "react";
// import {BrowserRouter as Router, Link, Routes, Route} from 'react-router-dom'

import { SuccessPage, CancelPage, LandingPageContent as LandingPage } from "./page" 

import {
  Route,
  Routes,
  useSearchParams,
} from "react-router-dom"

const routes = [{
    key: 'main',
    path: "/",
    element: <LandingPage />,
  },
  {
    key: 'success',
    path: "/success",
    element: <SuccessPage />,
  },
  {
    key: 'cancel',
    path: "/cancel",
    element: <CancelPage />,
  }
];

function App() {
	const [searchParams] = useSearchParams();
	const urlParams = new URLSearchParams(window.location.search);

  let lang = urlParams.get('lang');
  if ( !lang ) lang = searchParams.get('lang');
  if ( !lang ) lang = navigator.language || navigator.userLanguage; 

	window.__current_language = lang;
	
  return (
    <div className="site" >
        <Routes>
          {routes.map(item => {
            if (item.childs) {
              return (
                <Route key={`route-${item.path}`} element={item.element} path={item.path} >
                  {item.childs.map(child => <Route key={`route-${item.path}${child}`} path={child} element={null} />)}
                </Route>
              )
            } 
            return <Route key={`route-${item.path}`} element={item.element} path={item.path} />
          })}
        </Routes>
    </div>
  );
}


export default App;
